import selectStork from '@/components/selectStork.vue';
import { mapState, mapMutations } from 'vuex';
var _self,
	page = 1,
	timer = null;
export default {
	data() {
		return {
			newsList: [],
			row: '',
			partsCode: '',
			loadingText: '加载中...',
			pageNo: 1,
			isReach: false
		};
	},
	components: {
		selectStork
	},
	onLoad: function() {
		_self = this;
		// this.getnewsList();
	},
	onPullDownRefresh: function() {
		console.log('刷新');
		this.pageNo = 1;
		this.isReach = false;
		this.getnewsList();
	},
	onReachBottom: function() {
		console.log('加载');
		this.pageNo++;
		this.isReach = true;
		this.getnewsList();
	},
	computed: {
		...mapState(['app'])
	},
	methods: {
		...mapMutations(['urlRequest']),
		getnewsList: function(row) {
			this.loadingText = '加载中...';
			console.log(typeof row);
			if (typeof row=="string") {
				this.row = row;
				this.pageNo = 1;
				this.isReach = false;
			}
			this.urlRequest({
				url: `/operation-agency-service/warehouse/getHeadList?access_token=${this.app.access_token}`,
				method: 'post',
				title: '服务器出错',
				data: {
					isPage: true,
					pageNo: this.pageNo,
					pageSize: 10,
					params: { brandId: '', partsCode: this.partsCode, warehouseId: this.row || '' }
				},
				callBack: res => {
					if(res.data.result === '0000'&&this.partsCode){
					console.log(res.data.result);
						this.newsList===[]
						this.loadingText = '未搜索到相关数据！';
						return
					}else if(res.data.result.length > 0&&this.partsCode){
						this.newsList = res.data.result;
						this.loadingText = '没有更多数据！';
						return
					}
					if (res.data.result.length > 0 && res.data.result != '0000') {
						if (this.isReach) {
							for (let i of res.data.result) {
								this.newsList.push(i);
							}
							return;
						}
						this.newsList = res.data.result;
					} else {
						this.loadingText = '没有更多数据！';
					}
				}
			});
		},
		subumit(){
			this.isReach=false;
			this.getnewsList(1)
		},
		TO(e) {
			uni.navigateTo({
				url: `./inventoryOne/inventoryOne?id=${e}`
			});
		}
	}
};
